.aside {
  position: fixed;
  z-index: 2;
  top: 50px;
  right: 0px;
  width: 210px;
  padding: 10px;
  margin-right: -230px;
  background-color: rgba(250, 250, 250, 0.9);
  box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.2);
  transition: margin-right 0.3s, opacity 0.3s;
  border-radius: 8px 0 0 8px;
  opacity: 0;
}

.aside-enter-active,
.aside-enter-done {
  margin-right: 0px;
  opacity: 1;
}

.aside-item {
  height: 66px;
}

.aside-label {
  line-height: 20px;
  font-size: 16px;
}

.aside-input-item {
  width: 100%;
  height: 28px;
  padding-left: 5px;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  transition: border 0.2s;
}

.aside-input-item:focus {
  outline: none;
  border-radius: 5px;
  border: 1px solid rgb(126, 158, 255);
  box-sizing: border-box;
}

.aside-option:disabled {
  background-color: #eee;
  color: #36f;
  font-weight: bold;
  font-size: 15px;
}