.main {
  width: 100%;
  height: calc(100vh - 50px);
  background-color: #fafafa;
  box-sizing: border-box;
  padding: 0.5em 0.3em 0.5em 0.5em;
  margin-right: 5px;
  overflow-x: hidden;
  overflow-y: scroll;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(21em, 1fr));
  grid-auto-rows: minmax(min-content, max-content);
  gap: 0.5em;
}

.main-item {
  padding: 0.4em;
  background-color: rgb(240, 240, 240);
  border-radius: 8px;
  height: 100%;
  box-sizing: border-box;
  transition: background-color 0.3s;
  cursor: pointer;
  text-decoration: none;
  box-shadow: 0px 0px 3px rgb(190, 190, 190) inset;
}

.main-item-hyper {
  width: 100%;
  height: 100%;
}

.main-item:last-of-type {
  margin-bottom: 0.5em;
}

.main-item:hover {
  background-color: rgb(225, 225, 225);
  box-shadow: 1px 1px 5px rgb(100, 100, 100);
}

.main-item-seasonal:hover {
  background-color: rgb(250, 225, 225);
}

.main-item-event:hover {
  background-color: rgb(215, 235, 250);
}

.main-item-collab:hover {
  background-color: rgb(227, 246, 227);
}

.main-item-promo:hover {
  background-color: rgb(250, 250, 200);
}

.main-image-div {
  width: 3.5em;
  height: 3.8em;
  float: left;
  margin-right: 0.4em;
}

.main-image {
  width: 100%;
}

.main-id {
  position: relative;
  font-size: 0.65em;
  text-align: center;
  top: -0.65em;
  color: rgb(64, 64, 64);
}

.main-content {
  float: left;
}

.main-content-right {
  float: right;
}

.main-text {
  height: 1.5em;
  line-height: 1.5em;
  font-size: 0.8em;
  color: black;
  vertical-align: baseline;
}