.header {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: rgb(210, 232, 255);
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 3;
  height: 50px;
  padding: 5px;
  box-sizing: border-box;
}

.header-logo {
  height: 40px;
  width: 40px;
  margin-right: 5px; 
}

.header-title {
  font-size: 1em;
  color: #444;
  flex: 1;
}

.header-btn {
  float: right;
  width: 40px;
  height: 40px;
  line-height: 42px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  user-select: none;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  margin-left: 5px;
  transition: background-color 0.2s;
}

.header-btn:hover {
  background-color: rgba(0, 0, 0, 0.15);
}

.header-lang-btn {
  line-height: 38px;
  font-weight: bold;
  font-size: 18px;
}

.header-lang-content {
  position: relative;
  top: -8px;
  left: 9px;
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.7));
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
  visibility: hidden;
}

.header-lang-content-enter-active,
.header-lang-content-enter-done {
  opacity: 1;
  visibility: visible;
}

.header-lang-content-exit-active {
  visibility: hidden;
}

.header-lang-triangle {
  border-color: transparent transparent rgb(223, 238, 255) transparent;
  border-style: solid;
  border-width: 10px;
  width: 0;
}

.header-lang-option-div {
  position: relative;
  right: 40px;
  width: 100px;
  background: rgb(223, 238, 255);
  border-radius: 8px;
}

.header-lang-option:not(:last-of-type) {
  border-bottom: 1px solid rgb(204, 204, 204);
}

.header-lang-option:hover {
  background: rgb(139, 192, 248);
}

.header-lang-option:first-of-type {
  border-radius: 8px 8px 0 0;
}

.header-lang-option:last-of-type {
  border-radius: 0 0 8px 8px;
}